import { createGlobalStyle } from "styled-components";

export const GlobalStyle = createGlobalStyle`
html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  background: ${({ theme }) => theme.color.softPeach};
  max-width: 850px;
  margin: 0 auto;
  padding: 10px;
}

`;